import React, { useState, useEffect } from "react";
import axios from "axios";
import { useTranslation } from "react-i18next";
import diseasesData from "./data/diseases.json";
import symptomsData from "./data/symptoms.json";
import i18n from "./i18n";

// Translation helper
const tr = (symptomKey, lang) => symptomsData[symptomKey]?.[lang] || symptomsData[symptomKey]?.en || symptomKey;

// Health suggestions
const getSuggestions = (disease) => {
  const tips = {
    "Flu": [{ level: 1, text: "Rest well and drink fluids." }],
    "Migraine": [{ level: 1, text: "Avoid bright lights. Rest in a quiet room." }],
    "Common Cold": [{ level: 2, text: "Stay hydrated and rest." }]
  };
  return tips[disease] || [{ level: 3, text: "Consult a doctor if symptoms persist." }];
};

// Disease Info component
function DiseaseInfo({ disease, onBack }) {
  const data = diseasesData[disease];
  if (!data) return <p>Disease info not available.</p>;
  return (
    <div style={styles.infoPage}>
      <button style={styles.backBtn} onClick={onBack}> Back</button>
      <h2>{disease}</h2>
      <h3>Symptoms:</h3><ul>{data.symptoms.map(s => <li key={s}>{s}</li>)}</ul>
      <h3>Causes:</h3><ul>{data.causes.map(c => <li key={c}>{c}</li>)}</ul>
      <h3>Treatments:</h3><ul>{data.treatments.map(t => <li key={t}>{t}</li>)}</ul>
      <h3>Preventive:</h3><ul>{data.preventive.map(p => <li key={p}>{p}</li>)}</ul>
      <h3>More Info:</h3><ul>{data.links.map(l => <li key={l}><a href={l} target="_blank" rel="noreferrer">{l}</a></li>)}</ul>
    </div>
  );
}

// Main App
export default function App() {
  const { t } = useTranslation();
  const [page, setPage] = useState("welcome");
  const [symptoms, setSymptoms] = useState([]);
  const [selected, setSelected] = useState(new Set());
  const [result, setResult] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentDisease, setCurrentDisease] = useState(null);

  const lang = i18n.language;

  // Fetch symptoms
  useEffect(() => {
    if (page === "main") {
      axios.get("/symptoms")
        .then(res => setSymptoms(res.data.symptoms || []))
        .catch(console.error);
    }
  }, [page]);

  const toggle = s => {
    const ns = new Set(selected);
    ns.has(s) ? ns.delete(s) : ns.add(s);
    setSelected(ns);
  };

  const submit = () => {
    axios.post("/predict", { symptoms: Array.from(selected) })
      .then(res => setResult(res.data))
      .catch(console.error);
  };

  if (page === "info" && currentDisease) return <DiseaseInfo disease={currentDisease} onBack={() => setPage("main")} />;
  if (page === "welcome") return (
    <div style={styles.welcomePage}>
      <div style={styles.welcomeCard}>
        <h1 style={styles.appName}>HealthSync</h1>
        <p style={styles.tagline}>AI-based early health awareness</p>
        <button style={styles.getStartedBtn} onClick={() => setPage("main")}>{t("get_started")}</button>
        <div style={{ marginTop: 20 }}>
          <select onChange={e => i18n.changeLanguage(e.target.value)} style={styles.langSelector}>
            <option value="en">English</option>
            <option value="hi">Hindi</option>
            <option value="te">Telugu</option>
            <option value="ta">Tamil</option>
            <option value="kn">Kannada</option>
            <option value="es">Spanish</option>
          </select>
        </div>
      </div>
    </div>
  );

  return (
    <div style={styles.page}>
      <div style={styles.container}>
        <h1 style={styles.title}>{t("title")}</h1>
        <p style={styles.subtitle}>{t("select_symptoms")}</p>

        <div style={styles.twoColumn}>
          <div style={styles.leftColumn}>
            <input type="text" placeholder={t("search_placeholder")} value={searchTerm} onChange={e => setSearchTerm(e.target.value)} style={styles.searchBox}/>
            <div style={styles.symptomBox}>
              {symptoms.filter(s => s.toLowerCase().includes(searchTerm.toLowerCase())).map(s => (
                <label key={s} style={styles.symptomItem}>
                  <input type="checkbox" onChange={() => toggle(s)} style={{ marginRight: 8 }}/>
                  {tr(s, lang)}
                </label>
              ))}
            </div>
          </div>

          <div style={styles.rightColumn}>
            <div style={styles.selectedBox}>
              <h3>{t("selected_symptoms")}</h3>
              {Array.from(selected).length > 0
                ? Array.from(selected).map(s => <p key={s} style={styles.selectedItem}>{tr(s, lang)}</p>)
                : <p style={{ color: "#888" }}>{t("no_symptoms")}</p>}
            </div>

            <button onClick={submit} style={styles.predictBtn}>{t("predict")}</button>

            {result && result.disease && (
              <div style={styles.resultBox}>
                <h3 style={styles.resultTitle}>{result.disease}</h3>
                {result.probability && <p style={styles.resultProb}>Confidence: <strong>{(result.probability.top_probability*100).toFixed(2)}%</strong></p>}
                <button style={styles.viewInfoBtn} onClick={() => { setCurrentDisease(result.disease); setPage("info"); }}>View Details</button>
              </div>
            )}

            {result && (
              <div style={styles.suggestionBox}>
                <h3 style={styles.suggestionTitle}>*Health Suggestions</h3>
                {getSuggestions(result.disease).map((tip, idx) => (
                  <p key={idx} style={{ ...styles.suggestionText, marginLeft: tip.level*10, fontWeight: tip.level===1?600:400 }}>{tip.text}</p>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

